import Article, { Paragraph, Picture } from "./Article"

export default function ResetPassword() {
  return (
    <Article>
      <Paragraph
        title="A cualquiera le pasa"
        paragraph="No te sientas mal, tampoco te preocupes, en esta sección aprenderás a cómo restablecer tu contraseña."
      />

      <Paragraph
        title="Ubica el botón correcto"
        paragraph="En la pantalla de inicio sesión, el último botón nos sirve para poder poder acceder a la pantalla de solicitud de restablecimiento de contraseña, presiona sobre él."
      />

      <Picture url={"basics/resetPassword/1.png"} />

      <Paragraph
        title="Un pequeño formulario"
        paragraph="En la siguiente pantalla se te solicitara introduzcas tu usuario, puedes poner ya sea tu registro, correo electrónico o número telefónico, lo único que importa es que sea el tuyo, ya que con este dato podremos encontrar tu cuenta."
      />

      <Picture url={"basics/resetPassword/2.png"} />

      <Paragraph
        title="Si todo sale bien"
        paragraph="Si todo sale bien, te llegará un mensaje a tu correo electrónico, en él, vendrá un botón, el cual solamente tendrás que presionarlo e inmediatamente se te abrirá la pantalla de restablecimiento de contraseña dentro de la aplicación."
      />

      <Paragraph paragraph="Recuerda que el enlace tiene tiempo de caducidad, por lo que te recomendamos lo hagas en el momento en que recibas el mensaje." />

      <Picture url={"basics/resetPassword/3.png"} />

      <Paragraph
        title="Ahora sí, contraseña nueva"
        paragraph="En la pantalla de restablecimiento de contraseña se te mostrará otro pequeño formulario, en él se te solicita que ingreses tu nueva contraseña, la cuál debe de cumplir con ciertas características, también se te pedirá que la vuelvas a introducir, con la finalidad de que la corrobores."
      />

      <Picture url={"basics/resetPassword/4.png"} />

      <Paragraph
        title="A estrenar"
        paragraph="¡Y listo! Una vez guardada es ahora que inicies sesión para que puedas acceder a la aplicación."
      />
    </Article>
  )
}
