import { Flex, VStack } from "@react-native-material/core"
import Main from "../components/Main"
import { Text } from "react-native-paper"
import { Image, useWindowDimensions } from "react-native"
import { useState } from "react"

export default function Article({ children }) {
  return (
    <Main>
      <VStack
        // fill
        w="100%"
        p={25}
        // spacing={50}
        // pb={250}
        children={children}
      />
    </Main>
  )
}

export function Paragraph({ title, paragraph }) {
  return (
    <VStack
      pv={10}
      w="100%"
      // fill
      spacing={5}
    >
      {title && <Text variant="titleLarge">{title}</Text>}
      <Text variant="bodyLarge">{paragraph}</Text>
    </VStack>
  )
}

export function Picture({ url }) {
  const picture = url ? require(`../../assets/images/${url}`) : require(`../../assets/icon.png`)
  const { width } = useWindowDimensions()

  const [imageHeight, setImageHeight] = useState(300)
  const [imageWidth, setImageWidth] = useState(600)

  Image.getSize(picture, (width, height) => {
    setImageWidth(width)
    setImageHeight(height)
  })

  console.log((450 * imageHeight) / width)

  return (
    <Flex
      pv={10}
      w="100%"
      h={((width - 50) * imageHeight) / imageWidth}
      maxH={(450 * imageHeight) / imageWidth}
      maxW={450}
    >
      <Image
        resizeMode="contain"
        source={picture}
        style={{ flex: 1, width: "100%" }}
      />
    </Flex>
  )
}
