import Article, { Paragraph, Picture } from "./Article"

export default function Login() {
  return (
    <Article>
      <Paragraph
        title="Hola, queremos darte la bienvenida"
        paragraph="Antes de comenzar, queremos darte la bienvenida, así también agradecerte por usar nuestra aplicación y tomarte el tiempo para leer este manual de usuario. Esperamos que tu experiencia sea única y agradable."
      />

      <Paragraph
        title="Todo empieza con tus credenciales"
        paragraph="Para poder iniciar sesión es necesario que cuentes con tus credenciales, estas son tu registro y contraseña, también puedes usar tu correo electrónico o número telefónico que registrarte al darte de alta en tu servicio social o prácticas profesionales."
      />

      <Paragraph
        title="Conoce la pantalla"
        paragraph="En la pantalla de inicio de sesión podrás encontrar dos campos de textos, botones y unas opciones especiales."
      />

      <Picture url={"basics/login/1.png"} />

      <Paragraph
        title="Usuario y contraseña"
        paragraph="En el primer campo de texto debes introducir tu usuario, el cuál puede ser tu registro, correo electrónico o número telefónico. En el segundo campo de texto debes introducir tu contraseña."
      />

      <Paragraph paragraph="Si eres usuario nuevo o es la primera vez que inicias sesión, tu contraseña provisional es tu registro, esta contraseña es de un solo acceso, por lo que una vez iniciada la primera sesión se te pedirá que la actualices." />

      <Picture url={"basics/login/2.png"} />

      <Paragraph
        title="Antes continuar"
        paragraph="Abajo del campo de texto para la contraseña se encuentran unas funciones que tal vez sean de tu interés: la primera es la de mantener tu sesión abierta, con esto evitarás tener que estar introduciendo tu contraseña cada vez que entres a la aplicación; la segunda opción se habilita al activar la primera y consiste en la posibilidad de acceder a tu sesión mediante tu huella dactilar o FaceID."
      />

      <Paragraph paragraph="¡Activa la que quieras y creas te será más útil!" />

      <Paragraph
        title="¿Todo listo?"
        paragraph="Una vez hayas terminado, toca presionar el botón de iniciar sesión y ¡listo!, ¡ya estás dentro!"
      />

      <Picture url={"basics/login/3.png"} />

      <Paragraph
        title="¿Primera vez?"
        paragraph="Como te comentamos anteriormente, si es la primera vez que inicias sesión, se te pedirá que actualices tu contraseña de inmediato, si no lo haces no podrás acceder a la aplicación. Esto tienes que hacerlo por motivos de seguridad, con el fin de que nadie más que tú pueda acceder a tu información."
      />

      <Picture url={"basics/login/4.png"} />
    </Article>
  )
}
