import { Flex, VStack, Wrap } from "@react-native-material/core"
import { Text } from "react-native-paper"

export default function Container({ children, title }) {
  return (
    <>
      <Flex
        fill
        pv={25}
        ph={25}
      >
        <Text variant="headlineMedium">{title ?? "Título"}</Text>
      </Flex>
      <Flex>
        <Wrap
          ph={25}
          // style={{ backgroundColor: "#aaff00" }}
          // w="100%"
          // maxW={720}
          justify="start"
          // direction="column"
          spacing={15}
          children={children}
        />
      </Flex>
    </>
  )
}
