import { Flex } from "@react-native-material/core"
import { useNavigation } from "@react-navigation/native"
import { Image } from "expo-image"
import { ScrollView } from "react-native"
import { Button, Text, useTheme } from "react-native-paper"

export default function Main({ title, description, children, image }) {
  const theme = useTheme()
  const navigation = useNavigation()

  return (
    <ScrollView>
      <Flex
        w="100%"
        h={250}
      >
        <Flex
          w="100%"
          style={{ position: "absolute" }}
        >
          <Flex
            style={{ position: "absolute" }}
            w="100%"
            h="100%"
          >
            <Image
              source={image ? require(`../../assets/images/${image}`) : require("../../assets/icon.png")}
              style={{ width: "100%", height: "100%" }}
              contentFit="cover"
              blurRadius={10}
            />

            <Flex
              style={{ position: "absolute", backgroundColor: theme.colors.backdrop }}
              w="100%"
              h="100%"
            />
          </Flex>

          <Flex
            w="100%"
            maxW={1020}
            h={250}
            center
            style={{ alignSelf: "center" }}
          >
            <Flex
              style={{ position: "absolute" }}
              w="100%"
              h="100%"
            >
              <Image
                source={image ? require(`../../assets/images/${image}`) : require("../../assets/icon.png")}
                style={{ width: "100%", height: "100%" }}
                contentFit="cover"
              />
            </Flex>
          </Flex>
        </Flex>

        <Flex fill>
          <Flex
            style={{ position: "absolute", backgroundColor: theme.colors.backdrop }}
            w="100%"
            h="100%"
          />

          <Flex
            w="100%"
            maxW={1020}
            h={250}
            center
            style={{ alignSelf: "center" }}
          >
            <Flex
              maxW={450}
              ph={25}
            >
              <Text variant="headlineMedium">{title}</Text>
              <Text variant="bodyLarge">{description}</Text>
            </Flex>
          </Flex>
        </Flex>
      </Flex>

      <Flex
        fill
        w="100%"
        maxW={1020}
        pb={150}
        style={{ alignSelf: "center" }}
        children={children}
      />
    </ScrollView>
  )
}
