import { Text } from "react-native-paper"
import Main from "../components/Main"
import Container from "../components/Container"
import { Flex } from "@react-native-material/core"
import Flayer from "../components/Flayer"

export default function Basics() {
  return (
    <Main>
      <Container title="Autenticación">
        <Flayer
          title="Inicio de sesión"
          description="Aprende como acceder a la aplicación."
          screen="Inicio de sesión"
        />
        <Flayer
          title="Restablecer contraseña"
          description="¿Olvidaste tu contraseña? No te preocupes, te decimos qué tienes que hacer."
          screen="Restablece tu contraseña"
        />
      </Container>

      <Container title="Autenticación">
        <Flex>
          <Flayer
            title="A"
            description="A"
          />
        </Flex>
      </Container>
    </Main>
  )
}
